.event.single {
    width: 980px;
    margin: 0 auto;
    @media screen and (max-width: 960px) {
        width: 100%;
        margin: 0 auto; }
    .divider {
        display: grid;
        grid-template-columns: auto 25%;
        grid-gap: 10%;
        @media screen and (max-width: 1028px) {
            grid-gap: 0;
            grid-template-columns: auto; }
        .head-meta {
            .dates {
                font-size: 1.15rem;
                margin-bottom: .5em; }
            .main-title {
                font-size: 1.7rem;
                font-weight: 600;
                padding-bottom: 0px;
                @media screen and (max-width: 480px) {
                    font-size: 1.5rem; } }
            .separator {
                width: 9em;
                border: none;
                padding: .5px;
                background: orange;
                margin: .5rem 0; }
            .leads {
                font-size: 1.07rem;
                color: var(--leads);
                @media screen and (max-width: 672px) {
                    width: 100%;
                    font-size: .95rem; } }
            .button {
                font-size: .8em;
                font-weight: 600;
                padding: 0.75rem 1.5rem;
                border-radius: 7px; } }
        .section {
            margin-top: 1.7em;
            .row {
                display: grid;
                grid-template-columns: 3em auto; }
            .subtitle {
                margin-bottom: 1.5em;
                font-size: 1.2rem;
                font-weight: 600; }
            .icons {
                width: 3em;
                height: 3em;
                margin-right: 1em;
                img {
                    margin: 0; } }
            .info {
                .register {
                    margin-bottom: .75em;
                    @media screen and (max-width: 1028px) {
                        margin-right: 2em; } }
                .title-head {
                    padding-top: 0;
                    font-size: .95em;
                    font-weight: 600; }
                .value {
                    font-size: .875em; } }
            .when .info {
                @media screen and (max-width: 1028px) {
                    display: flex; }
                @media screen and (max-width: 448px) {
                    display: block; } } }
        .section.desc {
            .subtitle {
                margin-bottom: .5em; }
            p {
                font-size: .95em; } }
        .sidebar {
            margin-top: 1em;
            .grid-2 {
                @media screen and (min-width: 440px) {
                    grid-template-columns: auto auto; }
                @media screen and (min-width: 720px) {
                    grid-template-columns: auto auto auto; }
                @media screen and (min-width: 1028px) {
                    display: block; } }
            .row {
                @media screen and (min-width: 1028px) {
                        margin: 1em 0;
                        padding: 1em;
                        border-bottom: var(--border-line); } }
            .info .value {
                padding-top: .25em; }
            .plaftrom {
                @media screen and (min-width: 960px) {
                    border-bottom: 0; } } } } }
