.slider-con {
	position: relative;
	margin: auto;
	height: 13rem;
	overflow: hidden;
	@media screen and (min-width: 480px) {
		height: 18rem; }
	@media screen and (min-width: 720px) {
		height: 26rem; }
	@media screen and (min-width: 960px) {
		height: 30rem; }
	@media screen and (min-width: 1200px) {
		height: 33rem; }
	.slider {
		position: absolute;
		width: 100%; }
	.slider img {
		width: 100%;
		height: 13rem;
		object-fit: cover;
		margin: 0;
		border-radius: 20px;
		@media screen and (min-width: 480px) {
			height: 18rem; }
		@media screen and (min-width: 720px) {
			height: 26rem; }
		@media screen and (min-width: 960px) {
			height: 30rem; }
		@media screen and (min-width: 1200px) {
			height: 33rem; } }
	.slider.hiding {
		opacity: 0;
		animation-duration: 2s;
		animation-name: comout;
		@keyframes comout {
			from {
				opacity: 1; }
			to {
				opacity: 0; } } }
	.slider.active {
		animation-duration: 2s;
		animation-name: comin;
		@keyframes comin {
			from {
				opacity: 0; }
			to {
				opacity: 1; } } } }
